export default {
	data() {
		return {
			info:[],
		}
	},
	onLoad() {
		var t=this;
		uni.showLoading();
		//党组织详情
		t.urlRequest.urlRequest(t.func.getIntegralInfo,{communist_no:t.globalData.userInfo.communist_no},function(res){
			t.info=res.data.integral_info;
			uni.hideLoading();
		})
	},
	methods: {
		
	}
}
